import React, { useState, useRef } from "react";

interface MobileBeforeAfterSliderProps {
  beforeImg: string;
  afterImg: string;
  altBefore?: string;
  altAfter?: string;
}

const MobileBeforeAfterSlider: React.FC<MobileBeforeAfterSliderProps> = ({ beforeImg, afterImg, altBefore = "Before", altAfter = "After" }) => {
  const [sliderPos, setSliderPos] = useState(50);
  const containerRef = useRef<HTMLDivElement>(null);

  const handleTouchMove = (e: React.TouchEvent) => {
    if (!containerRef.current) return;
    const rect = containerRef.current.getBoundingClientRect();
    const touchX = e.touches[0].clientX - rect.left;
    let percent = (touchX / rect.width) * 100;
    percent = Math.max(0, Math.min(100, percent));
    setSliderPos(percent);
  };

  return (
    <div
      ref={containerRef}
      className="relative w-full max-w-xs mx-auto aspect-[3/4] overflow-hidden rounded-xl touch-pan-x select-none"
      onTouchMove={handleTouchMove}
      onTouchStart={handleTouchMove}
    >
      <img src={beforeImg} alt={altBefore} className="absolute inset-0 w-full h-full object-cover" />
      <img
        src={afterImg}
        alt={altAfter}
        className="absolute inset-0 h-full object-cover"
        style={{ width: `${sliderPos}%`, clipPath: `inset(0 ${100 - sliderPos}% 0 0)` }}
      />
      {/* Slider handle */}
      <div
        className="absolute top-0 bottom-0"
        style={{ left: `calc(${sliderPos}% - 12px)` }}
      >
        <div className="w-6 h-full flex items-center justify-center">
          <div className="w-1 h-16 bg-purple-500 rounded-full shadow-lg" />
        </div>
      </div>
      {/* Labels */}
      <span className="absolute left-2 top-2 bg-black/60 text-white text-xs px-2 py-1 rounded">{altBefore}</span>
      <span className="absolute right-2 top-2 bg-black/60 text-white text-xs px-2 py-1 rounded">{altAfter}</span>
    </div>
  );
};

export default MobileBeforeAfterSlider;
